.login-img:hover{
    cursor: pointer;
}

.no-mostrar{
    display: none;
}
.ocultar{
    visibility: hidden;
}
.icon-back{
    margin: 2rem 2rem;
    width: 10%;
}

.icon-back:hover{
    cursor: pointer;
}

.btn-back{
    border: none;
    background-color: transparent;
}
.btn-back:hover{
    cursor: pointer;
}

.btn-duals{
    @include grid(2,1rem);
}

.btn-option{
    margin-bottom: 2rem;
}

.swal2-popup {
    background: #ffffff;
}

.swal2-title {
  color:#000000;
}

swal2-styled.swal2-confirm {
    background-color:#5d14a2
}

.title{
    color: white;
    .title-p{
        top: 50%;
        position: absolute;
        text-align: center;
        width: 20%;
    }
    background-color: $terciario;
    img{ 
        position: relative;
        height: 100vh;
        width: 100%;
        filter: opacity(60%);
    }
}
.field{
    text-align: left;
}

.options-animation{
    align-self: center;
    margin: 0;
    width: 40%;
    p{
        font-size: 1.7rem;
    }
    .window-options{
        // width: 40%;
        background-color: white;
        border-radius: 1rem;
        padding: 1rem 2rem;
        img{
            width: 40%
        }
        .option-menu{
            text-align: center;
            border-radius: 0.5rem;
        }
        .option-menu:hover{
            cursor: pointer;
        }
    }
}
.options-animation:hover{
    cursor: pointer;
}

.shortcuts{
    @include telefono() {
        display: grid;
        grid-template-rows: repeat(5, 1fr);
        margin: 0 auto;
        top: 50%;
    }
    padding: 0rem 2rem;
    display: flex;
    justify-content: space-between;
    .btn-shortcut{
        margin: auto;
        height: 2rem;
        border: none;
        background-color: transparent;
        font-size: 1.7rem;
        font-weight: bold;
    }
    .btn-shortcut:hover{
        cursor: pointer;
        color: $principal;
    }
}

.nav-selection{
    display: grid;
    @include telefono() {
        grid-template-rows: repeat(3, 1fr);
    }
    @include tablet() {
        grid-template-columns: 1fr 3fr 1fr;
    }
}

.btn-remove{
    font-weight: bold;
    border: none;
    background-color: red;
    border-radius: 0.5rem;
    padding: 1rem 2rem;
    color: $color1;
    margin-bottom: 2rem;
}
.btn-remove:hover{
    cursor: pointer;
    background-color: darken($color: red, $amount: 13);
}
.btn-update{
    font-weight: bold;
    border: none;
    background-color: orange;
    border-radius: 0.5rem;
    padding: 1rem 2rem;
    color: $color1;
    margin-bottom: 2rem;
}
.btn-update:hover{
    cursor: pointer;
    background-color: darken($color: orange, $amount: 13);
}