.appointment{
    display: grid;
    grid-template-columns: 2fr 8fr;
    height: 100vh;
    .appointment-container{
        display: grid;
        grid-template-columns: 6fr 2fr;
        margin: 2rem;
        width: 96%;
        height: max-content;
        background-color: $grisClaro;
        border-radius: 1rem;
        .info-appointment{
            margin: 1rem;
            @include grid(2, 1rem);
            p{
                margin: 1rem 0rem;
                font-weight: bold;
            }
            span{
                font-weight: 400;
                color: $principal;
            }
            .products-services-appointment{
                grid-column: 1 / 3;
                .services-appointment,
                .products-appointment{
                    @include grid(4, 2rem);
                    span{
                        margin-left: 3rem;
                    }
                }
            }
        }
        
    }
    .actions-appointment{
        display: flex;
        flex-direction: column;
        margin: 2rem;
        h4{
            text-align: center;
        }
        button{
            margin: 1rem 0;
        }
    }
}