.owner-index{
    display: grid;
    grid-template-columns: 2fr 8fr;
    .citas{
        width: 90%;
        margin: auto;
        overflow-y: scroll;
        height: 70vh;
        grid-column: 2 /3;
        .appointment-confirmed{
            text-align: center;
            margin: 2rem 0;
            @include grid(4, 1rem);
            border-bottom: 2px solid $principal;
            p{
                margin: 0;
                height: fit-content;
            }
        }
        .services-appointment,
        .date-time-appointment{
            p{
                text-align: left;
                margin-top: 0.1rem;
            }
            display: flex;
            flex-direction: column;
        }
        .cancel-appointment{
            display: grid;
            place-items: center;
            height: min-content;
            button{
                margin: 0;
            }
        }
    }
    .title{
        grid-row: 1 / 3;
    }
    .nav-selection{
        width: 95%;
    }
}
.title-appointment{
    width: 90%;
    margin: auto;
    @include grid(4, 1rem);
    h1{
        font-size: 2.5rem;
        color: $principal;
    }
}