.dashboard{
    width: 90%;
    margin: auto;
}  
.windows{
    @include telefono() {
        @include grid-row(4, 1rem);
        .option-client {
            height: max-content;
            img {
                height: 20rem;
                width: 100%
            }
        }
    }
    @include tablet() {
        @include grid(4, 1rem);
    }
}
