.logo-dashboard{
    margin: 0 auto;
    width: 50%;
    @include tablet() {
        width: 60%;
    }
}
.shortcuts{
    @include telefono() {
        display: grid;
        grid-template-rows: repeat(5, 1fr);
        margin: 0 auto;
        top: 50%;
    }
    padding: 0rem 2rem;
    display: flex;
    justify-content: space-between;
    .btn-shortcut{
        margin: auto;
        height: 2rem;
        border: none;
        background-color: transparent;
        font-size: 1.7rem;
        font-weight: bold;
        z-index: 1;
    }
    .btn-shortcut:hover{
        cursor: pointer;
    }
}

.option-client{
    border-radius: 1rem;
    height: 80vh;
    background-color: $principal;
    position: relative;
    .title-dashboard{
        color: azure;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
    img{
        border-radius: 1rem;
        position: relative;
        height: 80vh;
        filter: opacity(60%);
    }
}
.option-client:hover{
    cursor: pointer;
}
.user-interaction{
    @include telefono() {
        gap: 10rem;
        .user-animation {
            img, button {
                height: 8rem;
            }
        }
        .options-animation {
            img {
                height: 5rem;
            }
        }
    }
    display: flex;
    justify-content: space-between;
    margin: auto;
    .user-animation{
        text-align: center;
        button{
            border: none;
            background-color: transparent;
            @include tablet() {
                width: 30%;
            }
        }
    }
