.owner-inventory{
    display: grid;
    grid-template-columns: 2fr 8fr;
    .nav-selection{
        width: 95%;
    }
}
.title-inventory{
    width: 90%;
    margin: auto;
    @include grid(5, 1rem);
    h1{
        font-size: 2.5rem;
        color: $principal;
    }
}
.btn-new-product{
    display: flex;
    flex-direction: row-reverse;
    margin-right: 5%;
}
.product-registered{
    width: 90%;
    margin: auto;
    text-align: center;
    align-items: center;
    @include grid(5, 1rem);
    span{
        color: $principal;
    }
    .img-product-inventory{
        margin: 1rem 0;
    }
    img{
        margin: auto;
        width: 50%;
    }
    border-bottom: 2px solid $principal;
}
.actions-product{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    button{
        margin: 1rem 0;
        width: min-content;
        height: min-content;
    }
    .btn-remove{
        margin-left: 2.5%;
    }
}
.show-inventory{
    overflow-y: scroll;
    height: 60vh;
}